/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import Header from "./header"
import "./layout.css"
import Footer from "./footer"
import TopNavigation from "./topNavigation"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
          description
        }
      }
    }
  `)

  React.useEffect(() => {
    window.addEventListener("scroll", function () {
      if (window.scrollY > document.querySelector("header").offsetHeight) {
        document.getElementById("mainNav").classList.add("fixed-top")
        if (window.innerWidth >= 992) {
          document.body.style.paddingTop = '5em'
        }
      } else {
        document.getElementById("mainNav").classList.remove("fixed-top")
        if (window.innerWidth >= 992) {
          document.body.style.paddingTop = 0
        }
      }
    })
  }, [])

  return (
    <>
      <Header
        siteTitle={data.site.siteMetadata.title}
        siteDescription={data.site.siteMetadata.description}
      />
      {/* <div
        style={{
          margin: `0 auto`,
          maxWidth: 960,
          padding: `0 1.0875rem 1.45rem`,
        }}
      >
        <main>{children}</main>
        <footer
          style={{
            marginTop: `2rem`,
          }}
        >
          Â© {new Date().getFullYear()}, Built with
          {` `}
          <a href="https://www.gatsbyjs.com">Gatsby</a>
        </footer>
      </div> */}
      <TopNavigation />
      <main data-bs-spy="scroll" data-bs-target="#mainNav" data-bs-offset="0">
        {children}
      </main>
      <Footer />
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
